  color: #FF4098;
}

.links {
  color: #ffffff;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
}

h1 {
  position: relative;
  padding-top: 40px;
  padding-bottom: 80px;
  font-size: 52px;
  line-height: 0.95;
  color: #ffffff;
}

h2 {
  margin: 1em 0 0.28em;
  font-size: 32px;
  color: #ffffff;
}

h3 {
  padding-top: 80px;
  text-align: left;
  font-size: 26px;
  color: #ffffff;
}

.h3sub {
  padding-top: 80px;
  text-align: center;
  font-size: 18px;
  color: #ffffff;
}

.h3sub-reg {
  padding-bottom: 80px;
  text-align: left;
  font-size: 18px;
