  color: #ffffff;
}

.h3about {
  margin: 80px 0;
  text-align: center;
  font-size: 15px;
  color: #ffffff;
}

h4 {
  font-size: 14px;
  text-transform: uppercase;
  color: #ffffff;
}

h5 {
  text-transform: uppercase;
  font-size: 14px;
  color: #ffffff;
}

.header {
  position: relative;
  background-image: url("../img/hr-img1.jpeg");
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  text-align: left;
  z-index: 1;
  transition: background .5s;
}

.header:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(#8B40C4, #8B40C4);
  opacity: 0.2;
  z-index: 5;
}

.header .container {
  position: relative;
  z-index: 6;
}

.header h1 {
  padding-top: 150px;
  padding-bottom: 50px;
  color: #ffffff;
  font-size: 18px;
}

.header h2 {
  padding-bottom: 50px;
}

.header__items {
  position: relative;
  display: flex;
  justify-content: space-between;
  padding: 30px 0;
  z-index: 10;
}

.header__items_bg {
  background-color: transparent;
}

.header__logo {
  padding: 0 30px;
}

.header__logo a {
  font-size: 24px;
  color: #ffffff;
}

.header__btn {
  padding: 10px 30px;
  box-sizing: border-box;
  background: transparent;
  border: 2px solid #ffffff;
  font-size: 18px;
  color: #ffffff;
  transition: background .3s;
}

.header__btn:hover {
  background: rgba(255, 255, 255, 0.2);
  color: #ffffff;
}

.header__artists {
  position: relative;
  background-image: url("/img/samuel-fyfe-235457.jpg");
  background-position: center center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  text-align: left;
  z-index: 1;
  transition: background .5s;
}

.header__artists:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(#8B40C4, #8B40C4);
  opacity: 0.4;
  z-index: 5;
}

.header__artists .container {
  position: relative;
  z-index: 6;
}

.header__artists h1 {
  padding-top: 80px;
  padding-bottom: 80px;
  color: #ffffff;
  font-size: 70px;
  text-transform: uppercase;
}

.header__artists h2 {
  padding-bottom: 50px;
}

