.menu-bnt {
  display: none;
  position: absolute;
  right: 5px;
  justify-content: center;
  align-items: center;
  box-sizing: content-box;
  width: 30px;
  height: 30px;
  padding-right: 15px;
  background: 0;
  border: 0;
  user-select: none;
  outline: 0;
}

.menu-bnt__icon {
  position: relative;
  display: inline-block;
  width: 30px;
  height: 3px;
  vertical-align: bottom;
  background: #ffffff;
  transition: background .3s .5s;
}

.menu-bnt__icon:before {
  top: 10px;
}

.menu-bnt__icon:after {
  top: -10px;
}

.menu-bnt__icon:before,
.menu-bnt__icon:after {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: 3px;
  background: #ffffff;
  transform-origin: 50% 50%;
  transition: top .3s .3s, transform .3s;
}

.menu-bnt__icon_show {
  background-color: transparent;
}

.menu-bnt__icon_show:before {
  transform: rotate3d(0, 0, 1, 45deg);
}

.menu-bnt__icon_show:after {
  transform: rotate3d(0, 0, 1, -45deg);
}

.menu-bnt__icon_show:before,
.menu-bnt__icon_show:after {
  transition: top .3s, transform .3s .5s;
  top: 0;
}

